body {
  background-color: #f9f5e8;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Add these CSS styles to your CSS file or component's style section */
.active-slide {
  opacity: 1;
  transition: opacity 0.7s ease; /* Adjust the transition duration as needed */
}

.inactive-slide {
  opacity: 0;
  transition: opacity 0.7s ease; /* Adjust the transition duration as needed */
}

.page21 {
  background-image: url("./Assets/limg.jpeg");
}
